body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
background: linear-gradient(-45deg, #9b7979, #4653eb );
}

#root {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
  
}

form {
  display: flex;
  flex-direction: column;
  grid-gap: 15px;
  gap: 15px;
  border: 1px solid black;
  border-radius: 5px;
  box-shadow: 0 0 8px;
  padding: 20px;
}

form label {
  display: flex;
  width: 100%;
  max-width: 500px;
  justify-content: flex-start;
  grid-gap: 10px;
  gap: 10px;
}

.error {
  color: rgb(131, 24, 24);
}

.submitBtn {
  height: 25px;
}
